.windowModal__container {
  position: relative;
  color: white;
  max-width: 550px;
  width: 100%;
  h3{
    color: #ffb400;
    text-transform: uppercase;
    padding: 10px 0 20px;
    font-weight: 700;
    text-align: center;
    font-size: 33px;
  }
}

.closeModal {
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
}

.windowModal__body {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.windowModal__info {
  padding: 0 5px;
  color: white;
  line-height: 20px;
  font-weight: 520;
  img {
    padding: 0 5px;
    color: white;
  }
  a {
    padding-left: 10px;
    color: #ffb400;
    text-decoration: underline;
  }
}

.img {
  margin-top: 25px;
  display: flex;
  justify-content: center;
  img{
    width: 100%;
    max-height: 300px;
  }
}
